body {
  --hover-color: #00acdc;
  margin: 0 auto;
  padding: 0;
  font-family:  Roboto, Ariel, sans-serif;
  display: block;
}

* {
  box-sizing: border-box;
}

ol, ul {
  padding: 0;
  text-decoration: none;
  list-style: none;
}

html, div, a {
  margin: 0;
  padding: 0;
}

img {
  width: 40px;
  height: 40px;
  display: block;
}

header {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
}

.container {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.logo__link {
  background: solid;
  justify-content: center;
  align-items: center;
  padding: 10px 0;
  display: flex;
}

.nav {
  height: 60px;
  display: flex;
}

.nav__list {
  align-items: center;
  display: flex;
}

.nav__link {
  text-transform: uppercase;
  color: #000;
  font-size: 12px;
  font-weight: 500;
  line-height: 14.06px;
  text-decoration: none;
}

.nav__item {
  flex-direction: column;
  align-items: center;
  padding: 22.5px 0;
  display: flex;
  position: relative;
}

.nav__item:not(:first-child) {
  margin-left: 20px;
}

.nav__item:hover .nav__link {
  color: var(--hover-color);
}

.is-active:after {
  content: "";
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.nav__item:hover .is-active:after {
  background-color: var(--hover-color);
}

